import { Component, OnInit, ViewChild } from '@angular/core';
import { DatatableComponent } from '@swimlane/ngx-datatable';
import { MeasurementAndDosageSizesService } from './../../services/MeasurementAndDosageSizesService';
import { AddAreaService } from '../../services/AddAreaService';
import { GetChemistDataService } from '../../services/GetChemistDataService';
import swal from 'sweetalert2';

@Component({
  selector: 'add-city',
  templateUrl: './city.component.html',
  styleUrls: [
    './city.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [MeasurementAndDosageSizesService, AddAreaService, GetChemistDataService]
})
export class CityComponent implements OnInit {
  rowsBasic = [];
  fullScreenRow = [];
  loadingIndicator = true;
  reorderable = true;
  packaging: any = {};
  city: any = [];
  packaging2: any = {};


  columns = [
    { prop: 'name' },
    { name: 'Gender' },
    { name: 'Company' }
  ];

  rows = [];
  expanded = {};
  timeout: any;


  rowsFilter = [];
  tempFilter = [];
  @ViewChild(DatatableComponent) table: DatatableComponent;

  cities_array = [];

  constructor(public _addMeasurementServices: MeasurementAndDosageSizesService,
    public _addAreaService: AddAreaService,
    public _getChemistDataService: GetChemistDataService) {
    this.getCities();
  }

  ngOnInit() {
  }

  getCities() {
    this._getChemistDataService.getAllCitiesService().subscribe(response => {
      if (response.code == 200) {
        console.log(response.data);
        this.cities_array = response.data;
        this.tempFilter = this.cities_array;
        this.rowsFilter = this.cities_array;
      }
    });
  }// Get cities End
  CityId : number = 0;
  openMyModal(Id) {
    console.log(Id);
    this.CityId = Id;
    var obj = this.cities_array.find(x => x.Id == Id);
    this.packaging2.name = obj.Name;
  }

  updateFilter(event) {
    const val = event.target.value.toLowerCase();

    // filter our data
    const temp = this.tempFilter.filter(function (d) {
      return d.CityName.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rowsFilter = temp;
    // Whenever the filter changes, always go back to the first page
    this.table.offset = 0;
  }

  onPage(event) {
    clearTimeout(this.timeout);
    this.timeout = setTimeout(() => {
      console.log('paged!', event);
    }, 100);
  }

  getRowHeight(row) {
    return row.height;
  }
  onclick() {
    alert("Hi!!");
  }
  //services
  addCity = {};
  loadServices() {
    console.log(this.packaging.name);
    this._addAreaService.addCity(this.packaging.name).subscribe(data => {
      this.cities_array.push(data.data);
      this.packaging = {};
    });
  }

  //Delete Services
  deleteCityServices(i) {
    // this.measurement.splice(i, 1);
    var self = this;
    swal({
      title: 'Are you sure?',
      text: 'You wont be able to revert',
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, delete it!'
    }).then(function (result) {
      console.log(result);
      if (result.value == true) {
        var index_number = self.cities_array.findIndex(x => x.Id == i);
          self.cities_array.splice(index_number, 1);
          self.rowsFilter = self.cities_array;
          self.tempFilter = self.cities_array;
        self._addAreaService.deleteCity(i).subscribe(response => {
          swal(
            'Deleted!',
            'Package has been deleted.',
            'success'
          );
        });
      }
      else {
        // alert('No deletion');
      }
    }).catch(swal.noop);
  }
  updateCity(){
    for(var i = 0; i < this.cities_array.length; i++){
      if(this.cities_array[i].Id == this.CityId){
        this.cities_array[i].Name = this.packaging2.name;
      }
    }
    this.tempFilter = this.cities_array;
    this.rowsFilter = this.cities_array;
    this._addAreaService.updateCity(this.CityId, this.packaging2.name).subscribe(response => {
      console.log(response);
    });
  }
}



